import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Scrollbar } from "swiper/modules";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

import "./styleReviews.scss";

import accountImg1 from "../../img/review1.jpg";
import accountImg2 from "../../img/review2.jpg";

export default function Reviews() {
  return (
    <section className="reviews" id="#reviews">
      <div className="reviews__body">
        <h3 className="reviews__title"></h3>
        <Swiper
          className="my-swiper"
          modules={[Pagination, Scrollbar]}
          spaceBetween={150}
          slidesPerView={2}
          pagination={{ clickable: true }}
        >
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg1}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "      
                        / , 
                     ."
              </p>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg2}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "-      /  
                 3       - 
                ."
              </p>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg1}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "      
                        / , 
                     ."
              </p>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg2}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "-      /  
                 3       - 
                ."
              </p>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg1}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "      
                        / , 
                     ."
              </p>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="item-reviews">
              <img
                className="item-reviews__img"
                src={accountImg2}
                alt=" "
              />
              <p className="item-reviews__name"> </p>
              <p className="item-reviews__text">
                "-      /  
                 3       - 
                ."
              </p>
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
    </section>
  );
}
